.about__img {
  border-radius: 50%;
  height: 160px;
  width: 160px;
}
.about__container {
  grid-template-columns: 3fr 9fr;
  column-gap: 1.875rem;
}
.about__data {
  padding: 1.875rem;
  background-color: var(--container-color);
  box-shadow: var(--shadow);
  border-radius: var(--border-radius);
  grid-template-columns: repeat(2, 1fr);
  column-gap: 1.875rem;
  align-items: flex-start;
  position: relative;
}
.about__data::before {
  content: "";
  width: 0;
  height: 0;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  border-right: 15px solid var(--container-color);
  position: absolute;
  left: -0.93rem;
  top: 20%;
}
.about__description {
  margin-bottom: 1rem;
  text-align: justify;
}
.about__skills {
  row-gap: 1.25rem;
}
.skills__titles {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
}
.skills__name {
  font-size: var(--normal-font-size);
  font-weight: var(--font-medium);
}
.skills__number {
  line-height: 1.2;
}
.skills__bar,
.skills__percentage {
  height: 7px;
  border-radius: 0.25rem;
}
.skills__bar {
  background-color: #f1f1f1;
}
.skills__percentage {
  display: block;
}
.react {
  width: 60%;
  background-color: #a7f0ba;
}
.figma {
  width: 60%;
  background-color: #ffd6e8;
}
.javascript {
  width: 65%;
  background-color: #d0e2ff;
}
.about__boxes {
  grid-template-columns: repeat(3, 1fr);
  column-gap: 1.875rem;
  margin-top: 6.25rem;
}
.about__box {
  display: flex;
  column-gap: 1.5rem;
}
.about__icon {
  font-size: var(--h1-font-size);
  color: #dedeea;
}

.about__title {
  font-size: 1.875rem;
}
@media screen and (max-width: 1024px) {
  .about__container {
    grid-template-columns: 720px;
    justify-content: center;
    row-gap: 1.875rem;
  }
  .about__data::before {
    border-left: 10px solid transparent;
    border-right: 10px solid transparent;
    border-bottom: 10px solid #fff;
    left: 49%;
    top: -20px;
  }
  .about__img {
    justify-self: center;
  }
  .about__box {
    flex-direction: column;
    row-gap: 0.5rem;
    text-align: center;
  }
}
@media screen and (max-width: 768px) {
  .about__container {
    grid-template-columns: 350px;
  }
  .about__data::before {
    left: 47%;
  }
  .about__data {
    grid-template-columns: lfr;
    row-gap: 1.875rem;
  }
  .about__boxes {
    grid-template-columns: repeat(2, 150px);
    row-gap: 1.5rem;
    justify-content: center;
  }
}
@media screen and (max-width: 576px) {
  .about__boxes {
    grid-template-columns: repeat(2, 150px);
    row-gap: 1.5rem;
    justify-content: center;
  }
  .about__title {
    font-size: 1.75rem;
  }
}
@media screen and (max-width: 350px) {
  .about__container {
    grid-template-columns: 250px;
  }
  .about__data::before {
    left: 47%;
  }
  .about__data {
    grid-template-columns: 1fr;
    row-gap: 1.875rem;
  }
  .about__boxes {
    grid-template-columns: repeat(1, 150px);
    justify-content: center;
  }
  .about__title {
    font-size: 1.25rem;
  }
}
